.section {
  display: flex;
  justify-content: center;
  padding-bottom: var(--section-bottom);
}

.section_inner {
  width: 144rem;
  padding: 2rem;
}

.section_title {
  padding-bottom: var(--title-h2-bottom);
  font-size: var(--title-h2-size);
}

.intro_text {
  font-size: 1.8rem;
  white-space: pre-line;
}

.part {
  padding-bottom: 3rem;
}

.part_title {
  padding-bottom: var(--title-h3-bottom);
  font-size: var(--title-h3-size);
}